import React from "react"
import AddTodo from  "./todo"
import TodoItem from "./todo.item"

const Todo=()=>{
  const [todos,settodos]=React.useState([]);
  const handleTodo =(text)=>{
     const newtodo={
      title:text,
      status:false,
      id:new Date().toDateString()+text
     
 }

 settodos([...todos,newtodo])
// console.log(text)
  };
  const handleToggle = (id) => {
    // JAVASCRIPT
    const todosAfterUpdation = todos.map(
      (todo) => (todo.id === id ? { ...todo, status: !todo.status } : todo)
      //
    )
    settodos(todosAfterUpdation);
    };
    const handleDelete = (id) => {
      // javascript
  
      const todosAfterDeletion = todos.filter((todo) => todo.id !== id);
  
      // React
      settodos(todosAfterDeletion);
    };
 // console.log(todos)
 return(
<div>
  <AddTodo handleTodo={handleTodo}/>
  
{
  todos.map((ele)=>(
    <TodoItem
    key={ele.id}
    id={ele.id}
    title={ele.title}
    status={ele.status}
    handleToggle={handleToggle}
     handleDelete={handleDelete}

/>
  ) )}

</div>
 ) 
};
export default Todo